import { db as fire } from 'auth/FirebaseAuth';
import DB from './db';
import axios from 'axios'
import { API_BASE_URL } from 'configs/AppConfig'

const url = API_BASE_URL+'/taps/';
const db = fire.collection("/taps");
const offlineDB = new DB('taps');

const getAll = () => {
  return db;
};

const create = (data) => {
  let id = ''+new Date().getTime();
  data['_id'] = id;
  return axios.post(url, data).then(result=>{
    offlineDB.create(data);
    db.doc(id).set(data);
    return true;
  }).catch(()=>{
    return false;
  })
};

const update = (id, value) => {  
  return axios.post(url, {'update':true, ...value,_id:id}).then(result=>{
    offlineDB.update(id,value).catch(()=>{});
    db.doc(id).update(value);
    return true;
  }).catch(()=>{
    return false;
  })
};

const remove = (id) => {
  offlineDB.delete(id).catch(()=>{});
  axios.post(url, {'delete':true, _id:id})
  return db.doc(id).delete();
};

const getRow = (id) => {
  return db.doc(id).get();
};

const getOnline = () => {
  return axios.post(url)
}

export const DataService = {
  getAll,
  getOnline,
  getRow,
  create,
  update,
  remove
};