import { db as fire } from 'auth/FirebaseAuth';
import DB from './db';
import axios from 'axios'
import { API_BASE_URL } from 'configs/AppConfig'

const url = API_BASE_URL+'/clientsBalance/';
const db = fire.collection("/clientsBalance");
const offlineDB = new DB('clientsBalance');

const getAll = () => {
  return db;
};

const create = (data) => {
  let id = ''+new Date().getTime();
  data['_id'] = id;
  offlineDB.create(data);
  axios.post(url, data)
  return db.doc(id).set(data);
};

const update = (id, value) => {
  offlineDB.update(id,value).catch(()=>{});
  axios.post(url, {'update':true, ...value,_id:id})
  return db.doc(id).update(value);
};

const remove = (id) => {
  offlineDB.delete(id).catch(()=>{});
  axios.post(url, {'delete':true, _id:id})
  return db.doc(id).delete();
};

const getRow = (id) => {
  return db.doc(id).get();
};

const getOnline = () => {
  return axios.post(url)
}

export const DataService = {
  getAll,
  getOnline,
  getRow,
  create,
  update,
  remove
};