let fetch = false;
let fetchIns = false;
let fetchTra = false;
let myData = [];
let inspecting = '';
let inspect = [];
let transacted = [];
let transactLength = 0;
const { Content } = Layout;
const { useBreakpoint } = Grid;
export const AppLayout = ({ navCollapsed, navType, location, direction, userData }) => {
  const currentRouteInfo = utils.getRouteInfo(navigationConfig[userData.role], location.pathname)
  const [allUsers, allUsersLoading] = useCollection(UsersService.getAll());	
  const [accounts, accountsLoading] = useCollection(AccountsService.getAll());	
  const [clients, clientsLoading] = useCollection(ClientsService.getAll());	
  const [taps, tapsLoading] = useCollection(TapsService.getAll());	
  const [employees, employeesLoading] = useCollection(EmployeesService.getAll());	
  const [inspections, inspectionsLoading] = useCollection(InspectionsService.getAll());	
  const [paid, paidLoading] = useCollection(PaidService.getAll());	
  const [payments, paymentsLoading] = useCollection(PaymentsService.getAll().orderBy('datePaid','desc'));	
  const [settings, settingsLoading] = useCollection(SettingsService.getAll());	
  const [expenses, expensesLoading] = useCollection(ExpensesService.getAll());	
  const [suppliers, suppliersLoading] = useCollection(SuppliersService.getAll());	
  const [sites, sitesLoading] = useCollection(SitesService.getAll());	

  const [borders, bordersLoading] = useCollection(BordersService.getAll());	
  const [countries, countriesLoading] = useCollection(CountriesService.getAll());	
  const [destinations, destinationsLoading] = useCollection(DestinationsService.getAll());	
  const [trucks, trucksLoading] = useCollection(TrucksService.getAll());	
  const [milleages, milleagesLoading] = useCollection(MilleagesService.getAll());	
  const [garages, garagesLoading] = useCollection(GarageService.getAll());	
  const [taxes, taxesLoading] = useCollection(TaxesService.getAll());	
  const [fuels, fuelsLoading] = useCollection(FuelService.getAll());	
  const [currencies, currenciesLoading] = useCollection(CurrenciesService.getAll());	
  const [trips, tripsLoading] = useCollection(TripService.getAll());	
  const [orders, ordersLoading] = useCollection(OrdersService.getAll());	
  const [roadTall, roadTallLoading] = useCollection(RoadTallService.getAll());	
  const [outsourcing, outsourcingLoading] = useCollection(OutsourceService.getAll());	
  const [garageTools, garageToolsLoading] = useCollection(ToolsService.getAll());	
  const [rental, rentalLoading] = useCollection(RentalService.getAll());	
  const [rentedTrucks, rentedTrucksLoading] = useCollection(RentedTrucksService.getAll());	
  const [loans, loansLoading] = useCollection(LoansService.getAll());	
  const [loanPayments, loanPaymentsLoading] = useCollection(LoanPaymentsService.getAll());	

  const screens = utils.getBreakPoint(useBreakpoint());
  const [monthDays] = React.useState({'1':31,'2':29,'3':31,'4':30,'5':31,'6':30,'7':31,'8':31,'9':30,'10':31,'11':30,'12':31})
  const [allUserLoading, setAllUserLoading] = React.useState(true);
  const [allUser, setAllUser] = React.useState([]);
  const [allEmployeesLoading, setAllEmployeesLoading] = React.useState(true);
  const [allEmployees, setAllEmployees] = React.useState([]);
  const [allTrucksLoading, setAllTrucksLoading] = React.useState(true);
  const [allTrucks, setAllTrucks] = React.useState([]);
  const [clientsBalance, setClientsBalance] = React.useState(myData);
  const [inspected, setInspected] = React.useState(inspect);
  const [transactions, setTransactions] = React.useState(transacted);
  const [setting, setSetting] = React.useState([]);
  const [setsLoading, setSetsLoading] = React.useState(true);
  const [drivers, setDrivers] = React.useState([]);
  const [site, setSite] = React.useState([]);
  const [siteEstimation, setSiteEstimation] = React.useState([]);
  const [sitePayments, setSitePayments] = React.useState([]);
  const [sitesArray, setSitesArray] = React.useState([]);
  const [sitesClientsArray, setSitesClientsArray] = React.useState([]);
  const [sitsLoading, setSitsLoading] = React.useState(true);
  const [account, setAccount] = React.useState([]);
	const [accountLoading, setAccountLoading] = React.useState(true);
	const [clientsCount, setClientsCount] = React.useState(0);

  const [userLoading, setUserLoading] = React.useState(true);
  const [users, setUsers] = React.useState([]);

  const [allCurrencyLoading, setallCurrencyLoading] = React.useState(true);
  const [allCurrency, setAllCurrency] = React.useState([]);

  const [allCountriesLoading, setAllCountriesLoading] = React.useState(true);
  const [allCountries, setAllCountries] = React.useState([]);

  const [allDestinationsLoading, setAllDestinationsLoading] = React.useState(true);
  const [allDestinations, setAllDestinations] = React.useState([]);

  const [allBordersLoading, setAllBordersLoading] = React.useState(true);
  const [allBorders, setAllBorders] = React.useState([]);

  const [allOrdersLoading, setAllOrdersLoading] = React.useState(true);
  const [allOrders, setAllOrders] = React.useState([]);

  const [allRentedTrucksLoading, setAllRentedTrucksLoading] = React.useState(true);
  const [allRentedTrucks, setAllRentedTrucks] = React.useState([]);

  const [allLoansLoading, setAllLoansLoading] = React.useState(true);
  const [allLoans, setAllLoans] = React.useState([]);

  const isMobile = !screens.includes('lg')
  const isNavSide = navType === NAV_TYPE_SIDE
  const isNavTop = navType === NAV_TYPE_TOP
  const getLayoutGutter = () => {
    if(isNavTop || isMobile) {
      return 0
    }
    return navCollapsed ? SIDE_NAV_COLLAPSED_WIDTH : SIDE_NAV_WIDTH
  }
 
  
  const monthNames = ["Jan", "Feb", "Mar", "Apr", "May", "Jun","Jul", "Aug", "Sep", "Oct", "Nov", "Dec"];
  const today = new Date();
  let todayS = today.getDay();
  todayS = todayS === 0 ? 7 : todayS;
  const mdiff = todayS - 1;
  const sdiff = 7 - todayS;
  const mondy = new Date().setDate(today.getDate()-mdiff);
  const lmondy = new Date().setDate(today.getDate()-(mdiff+7));
  const sundy = new Date().setDate(today.getDate()+sdiff);
  const lsundy = new Date().setDate(today.getDate()+(sdiff-7));
  const categories = [];
  const startDays = [];
  const endsDays = [];
  for(let i = 0; i<7; i++){
    let days = new Date(new Date().setDate(new Date(mondy).getDate()+i));
    let day = days.getDate();
    day = day < 10 ? '0' + day : day ;
    let month = new Date(new Date().setDate(new Date(mondy).getDate()+i)).getMonth();
    startDays.push(days.setHours(0,0,0,0));
    endsDays.push(days.setHours(23,59,59,999));
    categories.push(day + ' ' + monthNames[month]);
  }
  const findSeries = (data) => {
    let result = [];
    for(let i = 0; i < 7; i++){
      let dayTotal = data.filter(x=> x.id >= startDays[i] && x.id <= endsDays[i]).reduce((accumulator, currentElement) => {
        return parseFloat(accumulator) + parseFloat(currentElement.data().amount);
      },0)
      result.push(dayTotal);
    }
    return [{name: 'Weekly Income',data: result}];
  }
  const findMonths = (data) => {
    let lMonth = new Date(data).getMonth();
    let lYear = new Date(data).getFullYear();
    let dates = new Date().getDate();
    let month = new Date().getMonth();
    let year = new Date().getFullYear();
    let counter = 0;
    for(let j = lYear; j <= year; j++){
      let limiter = (lYear === year || j === year) ? month : 11;
      let starter = (lYear === year || j === lYear) ? lMonth : 0;
      for(let i = starter; i<= limiter; i++){
        counter += (j === year && i === month && dates < 5) ? 0 : 1;
      }
    }
    return counter;
  }

  
  let weekly = categories[0].split(" ")[1] === categories[6].split(" ")[1] ? categories[0].split(" ")[0] + ' - ' + categories[6] : categories[0] + ' - ' + categories[6];
	weekly += ', ' + new Date().getFullYear();

  function addDays(date, days) {
    const newDate = new Date(date);
    newDate.setDate(date.getDate() + days);
    return newDate;
  }

  const getPaye = (salary) => {
    let remaining = salary;
    let paye = 0;
    if(salary > 60000 && salary <= 100000){
      remaining = salary - 60000;
      paye = remaining * 0.1;
    }
    else if(salary > 100000 && salary <= 200000){
      remaining = salary - 100000;
      paye = 4000 + (remaining * 0.2);
    }
    else if(salary > 200000){
      remaining = salary - 200000;
      paye = 4000 + 20000 + (remaining * 0.3);
    }
    return paye
  }

  if(!currenciesLoading){
	  let allRecords = [];
    let lastCurrency = '';
    let changes = false;
	  currencies.docs.map((doc) => {
      let data = {'name': doc.data().name}
		  allRecords = {
        ...allRecords,
        [doc.data()._id] : {'name': doc.data().name},
      }
      lastCurrency = doc.data()._id;
      changes = !allCurrency[lastCurrency] || allCurrency[lastCurrency].name !== data.name ? true : changes;
		  return true;
	  })
    if(changes || (lastCurrency && (allCurrencyLoading || !allCurrency[lastCurrency]))){
      setAllCurrency(allRecords)
	  	setallCurrencyLoading(false);
	  }
	}

  if(!countriesLoading){
	  let allRecords = [];
    let lastCountry = '';
    let changes = false;
	  countries.docs.map((doc) => {
      let data = {'name': doc.data().name}
		  allRecords = {
        ...allRecords,
        [doc.data()._id] : {'name': doc.data().name},
      }
      lastCountry = doc.data()._id;
      changes = !allCountries[lastCountry] || allCountries[lastCountry].name !== data.name ? true : changes;
		  return true;
	  })
    if(changes || (lastCountry && (allCountriesLoading || !allCountries[lastCountry]))){
	  	setAllCountriesLoading(false);
      setAllCountries(allRecords)
	  }
	}

  
  if(!destinationsLoading){
	  let allRecords = [];
    let lastDestination = '';
    let changes = false;
	  destinations.docs.map((doc) => {
      let data = {'name': doc.data().name, 'country': doc.data().country}
		  allRecords = {
        ...allRecords,
        [doc.data()._id] : {'name': doc.data().name, 'country': doc.data().country},
      }
      lastDestination = doc.data()._id;
      changes = !allDestinations[lastDestination] || allDestinations[lastDestination].name !== data.name || allDestinations[lastDestination].country !== data.country ? true : changes;
		  return true;
	  })
    if(changes || (lastDestination && (allDestinationsLoading || !allDestinations[lastDestination]))){
	  	setAllDestinationsLoading(false);
      setAllDestinations(allRecords)
	  }
	}


  if(!rentedTrucksLoading){
	  let allRecords = [];
    let lastTruck = '';
    let changes = false;
	  rentedTrucks.docs.map((doc) => {
      let data = {'driverName': doc.data().driverName, 'phoneNumber': doc.data().phoneNumber, 'idNumber': doc.data().idNumber, 'passportNumber': doc.data().passportNumber, 'plateNumber': doc.data().plateNumber, 'trailerNumber': doc.data().trailerNumber}
		  allRecords = {
        ...allRecords,
        [doc.data()._id] : {'driverName': doc.data().driverName, 'phoneNumber': doc.data().phoneNumber, 'idNumber': doc.data().idNumber, 'passportNumber': doc.data().passportNumber, 'plateNumber': doc.data().plateNumber, 'trailerNumber': doc.data().trailerNumber},
      }
      lastTruck = doc.data()._id;
      changes = !allRentedTrucks[lastTruck] || allRentedTrucks[lastTruck].driverName !== data.driverName || allRentedTrucks[lastTruck].phoneNumber !== data.phoneNumber || allRentedTrucks[lastTruck].idNumber !== data.idNumber || allRentedTrucks[lastTruck].passportNumber !== data.passportNumber || allRentedTrucks[lastTruck].plateNumber !== data.plateNumber || allRentedTrucks[lastTruck].trailerNumber !== data.trailerNumber ? true : changes;
		  return true;
	  })
    if(changes || (lastTruck && (allRentedTrucksLoading || !allRentedTrucks[lastTruck]))){
	  	setAllRentedTrucksLoading(false);
      setAllRentedTrucks(allRecords)
	  }
	}


  if(!loansLoading){
	  let allRecords = [];
    let lastLoan = '';
    let changes = false;
	  loans.docs.map((doc) => {

      let paidAmount = 0;	
      paidAmount = loanPaymentsLoading ? 0 : loanPayments.docs.reduce((accumulator, currentElement) => {
        return parseFloat(accumulator) + parseFloat(currentElement.data().amount)
      },0)

      let data = {'bankName': doc.data().bankName, 'purpose': doc.data().purpose, 'amount': doc.data().amount, 'months': doc.data().months, 'interest': doc.data().interest, 'penalty': doc.data().penalty, paidAmount}
		  allRecords = {
        ...allRecords,
        [doc.data()._id] : {'bankName': doc.data().bankName, 'purpose': doc.data().purpose, 'amount': doc.data().amount, 'months': doc.data().months, 'interest': doc.data().interest, 'penalty': doc.data().penalty, paidAmount},
      }
      lastLoan = doc.data()._id;
      changes = !allLoans[lastLoan] || allLoans[lastLoan].bankName !== data.bankName || allLoans[lastLoan].purpose !== data.purpose || allLoans[lastLoan].amount !== data.amount || allLoans[lastLoan].months !== data.months || allLoans[lastLoan].interest !== data.interest || allLoans[lastLoan].penalty !== data.penalty || allLoans[lastLoan].paidAmount !== paidAmount ? true : changes;
		  return true;
	  })
    if(changes || (lastLoan && (allLoansLoading || !allLoans[lastLoan]))){
	  	setAllLoansLoading(false);
      setAllLoans(allRecords)
	  }
	}
    

  if(!bordersLoading){
	  let allRecords = [];
    let lastBorder = '';
    let changes = false;
	  borders.docs.map((doc) => {
      let data = {'name': doc.data().name, 'country': doc.data().country}
		  allRecords = {
        ...allRecords,
        [doc.data()._id] : {'name': doc.data().name, 'country': doc.data().country},
      }
      lastBorder = doc.data()._id;
      changes = !allBorders[lastBorder] || allBorders[lastBorder].name !== data.name || allBorders[lastBorder].country !== data.country ? true : changes;
		  return true;
	  })
    if(changes || (lastBorder && (allBordersLoading || !allBorders[lastBorder]))){
	  	setAllBordersLoading(false);
      setAllBorders(allRecords)
	  }
	}

  if(!employeesLoading){
	  let allRecords = [];
    let allDrivers = [];
    let lastUser = '';
    let changes = false;
	  employees.docs.map((doc) => {
      let pension = doc.data().salary*0.03;
      let maternity = doc.data().salary*0.003;
      let paye = getPaye(doc.data().salary);
      let cbhi = doc.data().netsalary*0.05;
      let data = {'name': doc.data().name, 'type': doc.data().type, 'phoneNumber':doc.data().phoneNumber, 'idNumber':doc.data().idNumber, 'salary':doc.data().salary, pension, maternity, paye,cbhi}
		allRecords = {
		  ...allRecords,
		  [doc.data()._id] : {'name': doc.data().name, 'type': doc.data().type, 'phoneNumber':doc.data().phoneNumber, 'idNumber':doc.data().idNumber, 'salary':doc.data().salary, pension, maternity, paye,cbhi},
		}
    if(doc.data().type == 'Driver' && !doc.data.truck) allDrivers.push(doc.data());
    lastUser = doc.data()._id;
    changes = !allEmployees[lastUser] || allEmployees[lastUser].name !== data.name || allEmployees[lastUser].type !== data.type || allEmployees[lastUser].phoneNumber !== data.phoneNumber || allEmployees[lastUser].idNumber !== data.idNumber || allEmployees[lastUser].salary !== data.salary
    ? true : changes;
		return true;
  })
  if(changes || (lastUser && (allEmployeesLoading || !allEmployees[lastUser]))){
      setAllEmployees(allRecords)
	  	setAllEmployeesLoading(false);
      setDrivers(allDrivers);
	  }
	}

  
  if(!trucksLoading){
	  let allRecords = [];
    let lastTruck = '';
    let changes = false;
	  trucks.docs.map((doc) => {
      let data = {'model': doc.data().model, 'plateNumber': doc.data().plateNumber, 'trailerNumber':doc.data().trailerNumber, 'meterNumber':doc.data().meterNumber, 'openingFuel':doc.data().openingFuel, 'driverName':doc.data().driverName}
      allRecords = {
        ...allRecords,
        [doc.data()._id] : {'model': doc.data().model, 'plateNumber': doc.data().plateNumber, 'trailerNumber':doc.data().trailerNumber, 'meterNumber':doc.data().meterNumber, 'openingFuel':doc.data().openingFuel, 'driverName':doc.data().driverName}
      }
    lastTruck = doc.data()._id;
    changes = !allTrucks[lastTruck] || allTrucks[lastTruck].model !== data.model || allTrucks[lastTruck].plateNumber !== data.plateNumber || allTrucks[lastTruck].trailerNumber !== data.trailerNumber || allTrucks[lastTruck].meterNumber !== data.meterNumber
    ? true : changes;
		return true;
  })
  if(changes || (lastTruck && (allTrucksLoading || !allTrucks[lastTruck]))){
      setAllTrucks(allRecords)
	  	setAllTrucksLoading(false);
	  }
	}


  if(!ordersLoading){
	  let allRecords = [];
    let lastOrder = '';
    let changes = false;
	  orders.docs.map((doc) => {
      let data = {'client':doc.data().client, 'item':doc.data().item, 'departure':doc.data().departure, 'destination':doc.data().destination}
      allRecords = {
        ...allRecords,
        [doc.data()._id] : {'client':doc.data().client, 'item':doc.data().item, 'departure':doc.data().departure, 'destination':doc.data().destination}
      }
    lastOrder = doc.data()._id;
    changes = !allOrders[lastOrder] || allOrders[lastOrder].client !== data.client || allOrders[lastOrder].item !== data.item || allOrders[lastOrder].departure !== data.departure || allOrders[lastOrder].destination !== data.destination
    ? true : changes;
		return true;
  })
  if(changes || (lastOrder && (allOrdersLoading || !allOrders[lastOrder]))){
      setAllOrders(allRecords)
	  	setAllOrdersLoading(false);
	  }
	}


  if(!allUsersLoading){
	  let allRecords = [];
    let lastUser = '';
	  allUsers.docs.map((doc) => {
		allRecords = {
		  ...allRecords,
		  [doc.data()._id] : {'name': doc.data().name, 'profile':doc.data().profile},
		}
    lastUser = doc.data()._id;
		return true;
	  })
	  if((lastUser && (allUserLoading || !allUser[lastUser]))){
      setAllUser(allRecords)
	  	setAllUserLoading(false);
	  }
	}
  
	if(!settingsLoading){
		let allRecords = [];
	  let lastSetting = '';
		settings.docs.map((doc) => {
		allRecords = {
		  ...allRecords,
		  [doc.data()._id] : {'value': doc.data().value},
		}
		lastSetting = doc.data()._id;
		return true;
		})
		if((lastSetting && (setsLoading || !setting[lastSetting]))){
			setSetting(allRecords)
			setSetsLoading(false);
		}
	}

  if(!clientsLoading && clients.docs.filter(x => x.data().status).length > 0){
    SettingsService.getOffline('clientsBalance').then((doc)=>{
      if(clientsBalance.length === 0 && doc.value.length && !fetch){
          setClientsBalance(JSON.parse(doc.value));
      }
    }).catch(()=>{})

    SettingsService.getOffline('inspected').then((doc)=>{
      if(inspected.length === 0 && doc.value.length && !fetchIns){
          // let jsonObj = JSON.parse(doc.value.length);
          // jsonObj = [...jsonObj];
          setInspected(doc.value);
      }
    }).catch(()=>{})
  }

  if(!accountsLoading){
		let allAccount = [];
	  	let lastAccount = '';
		  accounts.docs.map((doc) => {
			allAccount = {
			...allAccount,
			[doc.data()._id] : {bank: doc.data().bank,account: doc.data().account},
			}
			lastAccount = doc.data()._id;
			return true;
		})
		if((lastAccount && (accountLoading || !account[lastAccount]))){
			setAccount(allAccount)
			setAccountLoading(false);
		}
	}

  const countInSite = (site) => {
    let result = !clientsLoading ? clients.docs.filter(x=> x.data().site === site && x.data().status).length:0;
    return result;        
  }
  

  const rounder = (testvar) => {
    let holder = testvar + '';
    let result = holder.split('.').length === 1 ? testvar : (parseInt(holder.split('.')[1].slice(0,1)) + Math.round(parseFloat('0.'+holder.split('.')[1].slice(1)))) === 10 ? parseInt(holder.split('.')[0]) + 1 : 
    parseInt(holder.split('.')[0]) + ((parseInt(holder.split('.')[1].slice(0,1)) + Math.round(parseFloat('0.'+holder.split('.')[1].slice(1))))/10);
    return result;        
  }

  const totalClients = () => {
    let result = !clientsLoading ? clients.docs.filter(x => x.data().status).length: (<LoadingOutlined/>);
    return result;        
  }

  const getMonthlyAmount = (date,current) => {
    let time = ''+new Date(date).setHours(0,0,0,0);
    current = parseInt(current);
    let amount = (setting[time] && setting[time].value) 
                  ? setting[time].value : (setting[current]?setting[current].value:0);
    current = (setting[time] && setting[time].value) ? time : current;
    return {amount, current};
  }

  const singleMonth = (dateJoined) => {
    dateJoined = new Date(dateJoined).setHours(0,0,0,0);
    let data = setting['startDate'] ? setting['startDate'].value : dateJoined;
    let lMonth = new Date(data).getMonth();
    let lYear = new Date(data).getFullYear();
    let month = new Date(dateJoined).getMonth();
    let year = new Date(dateJoined).getFullYear(); 
    let counter = 0;
    let current = data;
    for(let j = lYear; j <= year; j++){
      let limiter = (lYear === year || j === year) ? month : 11;
      let starter = (lYear === year || j === lYear) ? lMonth : 0;
      for(let i = starter; i<= limiter; i++){
        let date = j+'-'+parseInt(i+1)+'-01';
        let info = getMonthlyAmount(date,current);
        current = info.current;
        counter = info.amount;
      }
    }
    return counter;
  }

  const checkDate = (date) => {
    return !date ? new Date(new Date().getFullYear(), new Date().getMonth
    ()).setDate(1) : date;
  }

  const getEnding = (date = new Date())=>{
    date = new Date(date).setHours(23, 59, 59, 999);
    let month = (new Date(date).getMonth())+1;
    return new Date(date).setDate(monthDays[month]); 
  }

  const estimatedInSite = (site,date) => {
    date= checkDate(date)
    let ending = getEnding(date);
    let estimated = inspected.filter(x=> +(x.datetime) >= date && +(x.datetime) <= ending && x.site === site).reduce((accumulator, currentElement) => {
      return parseFloat(accumulator) + parseFloat(currentElement.amount)
    },0)
    return estimated;
  }

  const totalEstimated = (date) => {
    date= checkDate(date)
    let estimated = 0;	
    let ending = getEnding(date);
    estimated = inspected.filter(x=> +(x.datetime) >= date && +(x.datetime) <= ending).reduce((accumulator, currentElement) => {
      return parseFloat(accumulator) + parseFloat(currentElement.amount)
    },0)
    return estimated;
  }

  const methodColors = (data) => {
    let colors = {
      'Cash':'cyan',
      'Cheque':'blue',
      'Mobile Money':'gold',
      'Bank Transfer':'magenta',
      'Not Paid':'volcano'
    }
    if(data.payCounter === 0){
      return (
        <Tag color='volcano' key={data._id}>
          Not Paid
        </Tag>
      )
    }
    else{
      for(let i=1; i<=data.payCounter; i++){
        return (<Tag color={colors[data[`method${i}`]]}>
          {data[`method${i}`]}
        </Tag>)
      }
    } 
  }

  const siteMonthlyPaid = (site,date) =>{
    date= checkDate(date)
    let estimated = 0;	
    let ending = getEnding(date);
    estimated = transactions.filter(x=> +(x.datetime) >= date && +(x.datetime) <= ending && x.site === site).reduce((accumulator, currentElement) => {
      return parseFloat(accumulator) + parseFloat(currentElement.amount)
    },0)
    estimated = !paidLoading ? paid.docs.filter(x=> +(x.data().datePaid) >= date && +(x.data().datePaid) <= ending && x.data().site === site).reduce((accumulator, currentElement) => {
      return parseFloat(accumulator) + parseFloat(currentElement.data().amount)
    },estimated) : estimated;
    return estimated;
  }

  if(!sitesLoading){
    let allRecords = [];
    let allSite = [];
    let allSiteClient = [];
    let allEstimation = [];
    let allPayments = [];
    let lastSite = '';
    sites.docs.map((doc) => {
      allRecords = {
        ...allRecords,
        [doc.data()._id] : {'name': doc.data().name,'siteId': doc.data().siteId,'price': doc.data().price,'myId': doc.data().myId},
      }
      
      allSite.push(`${doc.data().siteId} - ${doc.data().name}`)
      allSiteClient.push(countInSite(doc.id));
      allEstimation.push(estimatedInSite(doc.id));
      allPayments.push(siteMonthlyPaid(doc.id));
      lastSite = doc.data()._id;
      return true;
    })
    if((!clientsLoading && clientsCount !== clients.docs.filter(x => x.data().status).length) || (lastSite && (sitsLoading || !site[lastSite]))){
      setSiteEstimation(allEstimation);
      setSitePayments(allPayments);
      setSite(allRecords);
      setSitesClientsArray(allSiteClient);
      setSitesArray(allSite);
      setSitsLoading(false);
      if(!clientsLoading && clientsCount !== clients.docs.filter(x => x.data().status).length){
        setClientsCount(clients.docs.filter(x => x.data().status).length);
      }
    }
  }

  const updateCharts = () =>{
      if(!sitesLoading){
        let allEstimation = [];
        let allPayments = [];
        let lastSite = '';
        sites.docs.map((doc) => {
          let estimation = estimatedInSite(doc.id);
          allEstimation.push(estimation);
          allPayments.push(siteMonthlyPaid(doc.id));
          lastSite = doc.data()._id;
          return true;
        })
        setSiteEstimation(allEstimation);
        setSitePayments(allPayments);
      }
  }

  const monthlyPaid = (date) =>{
    let estimated = 0;	
    date = new Date(date).setHours(0,0,0,0);
    let ending = getEnding(date);
    estimated = transactions.filter(x=> +(x.datetime) >= date && +(x.datetime) <= ending).reduce((accumulator, currentElement) => {
      return parseFloat(accumulator) + parseFloat(currentElement.amount)
    },0)
    estimated = !paidLoading ? paid.docs.filter(x=> +(x.data().datePaid) >= date && +(x.data().datePaid) <= ending).reduce((accumulator, currentElement) => {
      return parseFloat(accumulator) + parseFloat(currentElement.data().amount)
    },estimated) : estimated;
    return estimated;
  }

  const getAmount = (setting,date,dateJoined,current) => {
    let time = ''+new Date(date).setHours(0,0,0,0);
    dateJoined = ''+new Date(new Date(dateJoined).setDate(1)).setHours(0,0,0,0);
    current = parseInt(current);
    dateJoined = parseInt(dateJoined);
    if(current < dateJoined){
      return {amount: 0, current}
    }
    current = ''+current;
    dateJoined = ''+dateJoined;
    let amount = (setting[time] && setting[time].value) 
                  ? setting[time].value : setting[current].value;
    current = (setting[time] && setting[time].value) ? time : current;
    return {amount, current};
  }

  const unpaidBalance = (client) => {
    let balance = !clientsBalance[client.id] ? 0 : clientsBalance[client.id];
    if(client.data().type === 2){
      let body = !paidLoading ? [...paid.docs] : []; 
      body = body.filter(x => +(x.data().clientId) === +(client.id));
      let total = body.reduce((accumulator, currentElement) => {
        return parseFloat(accumulator) + parseFloat(currentElement.data().amount);
      },0)
      balance -= total;
    }
    return balance;
  }

  const unpaidMonths = (client) => {
    let dateJoined = client.dateJoined;
    let data = setting['startDate'].value;
    let lMonth = new Date(data).getMonth();
    let lYear = new Date(data).getFullYear();
    let month = new Date().getMonth();
    let year = new Date().getFullYear(); 
    let counter = 0;
    let current = data;
    let historic = [];
    for(let j = lYear; j <= year; j++){
      let limiter = (lYear === year || j === year) ? month : 11;
      let starter = (lYear === year || j === lYear) ? lMonth : 0;
      for(let i = starter; i<= limiter; i++){
        let date = j+'-'+parseInt(i+1)+'-01';
        let info = getAmount(setting,date,dateJoined,current);
        current = info.current;
        let id = client._id+''+info.current
        let check = !paidLoading ? paid.docs.filter(x=> ''+x.id===id) : [];
        let checks = check.length === 0 ? info.amount : info.amount - check[0].data().paid;
        if(checks > 0){
          historic.push({month:(parseInt(i+1)<10?'0':'')+parseInt(i+1)+'-'+j, amount: checks})
        }
        counter += checks;
      }
    }
  
    return {counter,historic}
  }

  const siteMonthlyPaidClients = (site,month) =>{
    if(paidLoading) return (<LoadingOutlined/>)
    let fees = singleMonth(month);
    let body = [...paid.docs]; 
		body = body.filter(x => x.data().month === month && x.data().site === site && x.data().paid === fees);
    return body.length;
  }

  const getColor = (total, paid) => {
    if(paid === 0) return 'volcano'
    else if(paid === total) return 'cyan'
    else return 'gold'
  }

  let avatarColors = ['#04d182','#fa8c16','#1890ff','#ffc542','#ff6b72','#f50','#2db7f5','#87d068','#108ee9'];
  let types = ['Pieces', 'Sq Meter', 'Meter', 'Kilogram', 'Packet', 'Dozen'];
  let shortTypes = ['Pcs', 'Sqm', 'M', 'Kg', 'Pkt', 'Dzn'];
  const systemData = {
    users,
    userLoading,
    allUser,
    allUserLoading,
    allUsers,
    allUsersLoading,
    accounts,
    accountsLoading,
    account,
    accountLoading,
    clients,
    clientsLoading,
    clientsBalance,
    employees,
    employeesLoading,
    inspected,
    inspections,
    inspectionsLoading,
    paid,
    paidLoading,
    payments,
    paymentsLoading,
    settings,
    settingsLoading,
    setting,
    setsLoading,
    sites,
    sitesLoading,
    taps,
    tapsLoading,
    site,
    sitsLoading,
    userData,
    sitesArray,
    sitesClientsArray,
    expenses,
    expensesLoading,
    suppliers,
    suppliersLoading,
    borders,
    bordersLoading,
    countries, 
    countriesLoading,
    destinations,
    destinationsLoading,
    trucks,
    trucksLoading,
    allTrucks,
    allTrucksLoading,
    milleages,
    milleagesLoading,
    garages, 
    garagesLoading,
    taxes,
    taxesLoading,
    fuels, 
    fuelsLoading,
    currencies, 
    currenciesLoading,
    allCurrency, 
    allCurrencyLoading,
    allCountries, 
    allCountriesLoading,
    allDestinationsLoading,
    allDestinations,
    allBordersLoading,
    allBorders,
    trips, 
    tripsLoading,
    orders, 
    ordersLoading,
    roadTall, 
    roadTallLoading,
    allOrders,
    allOrdersLoading,
    outsourcing, 
    outsourcingLoading,
    garageTools, 
    garageToolsLoading,
    recentClients: !clientsLoading?clients.docs.reverse().slice(0,4) : [],
    mondy: new Date(mondy).setHours(0,0,0,0),
    lmondy: new Date(lmondy).setHours(0,0,0,0),
    sundy: new Date(sundy).setHours(23,59,59,999),
    lsundy: new Date(lsundy).setHours(23,59,59,999),
    categories,
    weekly,
    types,
    shortTypes,
    avatarColors,
    siteEstimation,
    sitePayments,
    transactions,
    drivers, 
    allEmployees,
    allEmployeesLoading,
    rental,
    rentalLoading,
    rentedTrucks,
    rentedTrucksLoading,
    allRentedTrucks,
    allRentedTrucksLoading,
    loans, 
    loansLoading,
    loanPayments, 
    loanPaymentsLoading,
    allLoans,
    allLoansLoading,
    addDays,
    setDrivers,
    methodColors,
    rounder,
    findSeries,
    findMonths,
    getColor,
    countInSite,
    singleMonth,
    estimatedInSite,
    totalEstimated,
    totalClients,
    monthlyPaid,
    siteMonthlyPaid,
    siteMonthlyPaidClients,
    unpaidBalance,
    getAmount,
    unpaidMonths,
    getEnding

  }
  const { status } = useThemeSwitcher();

  if (status === 'loading') {
    return <Loading cover="page" />;
  }

  const getLayoutDirectionGutter = () => {
    if(direction === DIR_LTR) {
      return {paddingLeft: getLayoutGutter()}
    }  
    if(direction === DIR_RTL) {
      return {paddingRight: getLayoutGutter()}
    }
    return {paddingLeft: getLayoutGutter()}
  }
  return (
    <Layout>
      <HeaderNav isMobile={isMobile} userData={userData} systemData={systemData}/>
      {(isNavTop && !isMobile) ? <TopNav routeInfo={currentRouteInfo}/> : null}
      <Layout className="app-container">
        {(isNavSide && !isMobile) ? <SideNav userData={userData} routeInfo={currentRouteInfo}/> : null }
        <Layout className="app-layout" style={getLayoutDirectionGutter()}>
          <div className={`app-content ${isNavTop ? 'layout-top-nav' : ''}`}>
            <PageHeader display={currentRouteInfo?.breadcrumb} title={currentRouteInfo?.title} />
            <Content>
              <AppViews systemData={systemData}/>
            </Content>
          </div>
          <Footer />
        </Layout>
      </Layout>
      {isMobile && <MobileNav userData={userData}/>}
    </Layout>
  )
}

const mapStateToProps = ({ theme }) => {
  const { navCollapsed, navType, locale } =  theme;
  return { navCollapsed, navType, locale }
};

export default connect(mapStateToProps)(React.memo(AppLayout));