import { db as fire } from 'auth/FirebaseAuth';
import DB from './db';

const db = fire.collection("/settings");
const offlineDB = new DB('settings');

const getAll = () => {
  return db;
};

const create = (data,id) => {
  id = ''+id;
  data['_id'] = id;
  offlineDB.create(data).catch(()=>{offlineDB.update(id,data)});
  return db.doc(id).set(data);
};

const saveOffline = (data,id) => {
  id = ''+id;
  data['_id'] = id;
  return offlineDB.create(data).catch(()=>{offlineDB.update(id,data)});
};

const update = (id, value) => {
  offlineDB.update(id,value).catch(()=>{});
  return db.doc(id).update(value);
};

const remove = (id) => {
  offlineDB.delete(id).catch(()=>{});
  return db.doc(id).delete();
};

const getOffline = (id) => {
  return offlineDB.getRow(id)
};

const getRow = (id) => {
  return db.doc(id).get();
};

export const DataService = {
  create,
  getAll,
  getOffline,
  getRow,
  remove,
  saveOffline,
  update,
};